.promo {
  background-color: #073042;
  color: #fff;
}

.promo__container {
  padding-top: 75px;
  padding-bottom: 75px;
  gap: 20px;
  justify-content: space-between;
}

.promo__about {
  max-width: 640px;
  display: flex;
  flex-direction: column;
  gap: 36px;
}

.promo__text {
  max-width: 310px;
  font-weight: 400;
  font-size: 13px;
  line-height: 1.21;
  color: rgba(255, 255, 255, .5);
  margin: 0;
  padding: 0;
}

.promo__title {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 50px;
  line-height:  58px;
}

.promo__link {
  min-width: 129px;
  width: min-content;
  min-height: 36px;
  box-sizing: border-box;
  display: flex;
  border: 1px solid rgba(255, 255, 255, .6);
  border-radius: 1px;
  margin-top: 110px;

  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-decoration: none;
  color: #fff;
  align-items: center;
  justify-content: center;
  
}

.promo__logo {
  width: 310px;
  height: 304px;
}

@media screen and (max-width: 880px) {
  .promo__container {
    flex-direction: column-reverse;
    align-items: center;
    gap: 48px;
    padding-top: 140px;
    padding-bottom: 76px;
  }

  .promo__title {
    font-size: 40px;
    line-height: 50px;
    text-align: center;
  }

  .promo__about {
    gap: 22px;
    align-items: center;
  }

  .promo__text {
    text-align: center;
  }

  .promo__link {
    margin-top: 70px;
  }
}

@media screen and (max-width: 580px) {
  .promo__container {
    padding-top: 70px;
    padding-bottom: 30px;
    gap: 44px;
  }
 
  .promo__logo {
    width: 210px;
    height: 206px;
  }

  .promo__title {
    font-size: 30px;
    line-height: 38px;
  }

  .promo__link {
    margin-top: 58px;
  }
}