.hamburger-button {
  display: none;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 26px;
  right: 30px;
  width: 28px;
  height: 23px;
  cursor: pointer;
  z-index: 5;
  transition: all .3s;
  border: none;
  background: none;
  padding: 0;
  margin: 0;
}

.hamburger-button:focus-visible {
  outline: 1px solid #fff;
  border-radius: 5px;
}

.hamburger-button_off > span,
.hamburger-button_off > span::before,
.hamburger-button_off > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: #000;
  transition: all .5s;
}

.hamburger-button_light > span,
.hamburger-button_light > span::before,
.hamburger-button_light > span::after {
  background-color: #fff;
}

.hamburger-button_off > span::before {
  content: '';
  top: -10px;
}

.hamburger-button_off > span::after {
  content: '';
  top: 10px;
}

.hamburger-button_on > span,
.hamburger-button_on > span::before,
.hamburger-button_on > span::after {
  display: block;
  position: absolute;
  width: 22px;
  height: 2.5px;
  background-color: #000;
  transform: rotate(45deg);
  transition: all .5s;
}

.hamburger-button_on > span::before {
  content: '';
  top: 0;
  transform: rotate(0);
}

.hamburger-button_on > span::after {
  content: '';
  top: 0;
  transform: rotate(90deg);
}

@media screen and (max-width: 880px) {
  .hamburger-button {
    display: flex;
  }
}

@media screen and (max-width: 580px) {
  .hamburger-button {
    right: 23px;
    width: 22px;
  }

  .hamburger-button > span,
  .hamburger-button > span::before,
  .hamburger-button > span::after {
    height: 2.5px;
  }

}