.main {
  width: 100%;
  margin: 0;
  padding: 0;
}

.section {
  width: 100%;
  display: flex;
  justify-content: center;
}

.section__container {
  box-sizing: border-box;
  max-width: 1280px;
  width: 100%;
  padding-right: 70px;
  padding-left: 70px;
  display: flex;
}

.section__title {
  width: 100%;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.21;
  border-bottom: 1px solid #000;
  margin: 0;
  padding: 0 0 23px;
}

@media screen and (max-width: 880px) {
  .section__container {
    padding-right: 50px;
    padding-left: 50px;
  }
}

@media screen and (max-width: 580px) {
  .section__container {
    padding-right: 18px;
    padding-left: 18px;
  }

  .section__title {
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
    padding-bottom: 28px;
  }
}